.NotificationWrapper {
  display: flex;
  flex-direction: row;
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 20px;
  box-shadow: 2px 2px 10px 5px #ccc;
  width: 350px;
  height: 90px;
  align-items: center;
}

.NotificationIcon {
  width: 50px;
  height: 50px;
  background-color: white;
  border-radius: 8px;
  margin-top: 25px;
  margin-bottom: 25px;
  margin-left: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
}

.NotificationText {
  font-size: 13.5px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: white;
  display: flex;
  flex-direction: column;
  width: 260px;
  margin-left: 10px;
}
