.AQIWrapper {
  width: 150px;
  height: 150px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px #ccc;
}

.AQIValue {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 10px;
  font-size: 60px;
  font-weight: bold;
}

.AQILocation {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 13px;
}
