.ScanLine {
    position: absolute;
    background-color: aqua;
    box-shadow: 0 0 12px aqua;
    height: 10px;
    width: 100vw;
    border-radius: 50%;
}

.Scanner {
    position: absolute;
    background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
    height: 100%;
    width: 20%;
    border-radius: 50%;
    animation: scanner-loop 3s ease-in-out infinite;
}

@keyframes scanner-loop
{
  0% 
  {
    left: -10%;
  }
  50%
  {
    left: 90%;
  }
  100%
  {
    left: -10%;
  }
}

.ScanText {
  position: absolute;
  font-weight: bold;
  font-size: 40px;
  left: 100px;
  bottom: 20px;
  color: rgba(89,39,229,1);
}