.SwaggerTagWrapper {
    position: absolute;
    margin: 0;
    border-radius: 4px;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.19);
    background-color: white;
    width: 100%;
    height: 100%;
}

.SwaggerTagSummary {
    display: flex;
    align-items: center;
    padding: 5px;
    cursor: pointer;
    -webkit-box-align: center;
}

.SwaggerTagSummaryMethod {
    font-size: 14px;
    font-weight: 700;
    min-width: 60px;
    padding: 6px 15px;
    text-align: center;
    border-radius: 3px;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
    color: #fff;
    font-family: Titillium Web, sans-serif;
    background: #61affe;
}

.SwaggerTagSummaryPath {
    font-size: 12px;
    display: flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 3 auto;
    flex: 0 3 auto;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    word-break: break-all;
    padding: 0 10px;
    font-family: Source Code Pro, monospace;
    color: #3b4151;
}

.SwaggerTagSummaryDescription {
    font-size: 13px;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    font-family: Open Sans, sans-serif;
}

.Authorization {
    padding: 0 10px;
    border: none;
    background: none;
    outline: none;
}