.Header {
  display: flex;
  text-align: center;
  height: 110vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
  z-index: 10;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(255, 255, 255, 1) 85%,
    rgba(255, 255, 255, 1) 100%
  );
}

h1 {
  position: absolute;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  margin: 0;
}

.Mouse {
  opacity: 0.5;
  width: 25px;
  height: 45px;
  border: 3px solid #333;
  border-radius: 60px;
}
.Mouse::before {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  border-radius: 50%;
  opacity: 1;
  animation: wheel 2s infinite;
  -webkit-animation: wheel 2s infinite;
}

@keyframes wheel {
  to {
    opacity: 0;
    top: 30px;
  }
}

@keyframes wheel-mobile {
  to {
    opacity: 0;
    top: 0px;
  }
}

@media (max-width: 1224px) and (orientation: portrait) {
  .Header {
    font-size: small;
  }
  .Mouse {
    border: 0px;
  }
  .Mouse::before {
    top: 30px;
    width: 20px;
    height: 20px;
    animation: wheel-mobile 2s infinite;
    -webkit-animation: wheel-mobile 2s infinite;
  }
}