.FaceModal {
  position: absolute;
  width: 45%;
  height: 60%;
  background-color: white;
  border-radius: 10px;
  box-shadow: 2px 2px 10px #ccc;
  z-index: 100;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (max-width: 1224px) {
  .FaceModal {
    width: 70%;
    height: 50%;
  }
}
