.Screen {
  position: relative;
  display: grid;
  height: 100vh;
  width: 100vw;
  place-content: center;
  place-items: center;
}

.Progress {
  position: absolute;
  top: 32px;
  left: 32px;
  display: grid;
  border-radius: 3px;
  height: calc(100% - 64px);
  width: 8px;
}

.ProgressInner {
  background-color: transparent;
  background-image: linear-gradient(
    rgba(89, 39, 229, 1) 0%,
    rgba(89, 39, 229, 1) 27%,
    rgba(148, 110, 255, 1) 100%
  );
  background-repeat: no-repeat;
  background-size: 100% 0%;
  border-radius: 3px;
}

.Circle1 {
  position: absolute;
  top: 8%;
  left: 27.5px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(89, 39, 229, 1);
  border: 1px solid white;
  z-index: 10;
}

.Circle2 {
  position: absolute;
  top: 37%;
  left: 27.5px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(89, 39, 229, 1);
  border: 1px solid white;
  z-index: 10;
}

.Circle3 {
  position: absolute;
  top: 58%;
  left: 27.5px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
