@media (max-width: 1224px) {
  .Screen {
    font-size: 12px;
  }
  .Progress {
    top: 15px;
    left: 15px;
  }
  .Circle1 {
    top: 6%;
    left: 10.5px;
  }
  .Circle2 {
    top: 35%;
    left: 10.5px;
  }
  .Circle3 {
    top: 56%;
    left: 10.5px;
  }
  .Circle4 {
    top: 77%;
    left: 10.5px;
  }
}
