.Footer {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 100vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
  background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,0.4) 20%, rgba(255,255,255,0.4) 100%);
  z-index: 10;
  position: absolute;
}

.Icons {
  margin-right: 50px;
  color: black;
}

.Icons:hover,
.Icons:active,
.Icons.active {
  color: rgb(89, 39, 239);
}

@media (max-width: 1224px) {
  .TLDSString {
    font-size: 15px;
    margin: 15px;
  }
}